<template>
    <div id="app">
        <search-page v-if="!isShowResult" @onSearch="onSearch"></search-page>
        <search-result v-else :search-query="searchQuery" :result="result"></search-result>
    </div>
</template>
<script>
import SearchPage from '@/components/SearchPage'
import SearchResult from '@/components/SearchResult'
export default {
    name: 'App',
    components: {
        SearchPage,
        SearchResult
    },
    data() {
        return {
            isShowResult: false,
            searchQuery: {},
            result: []
        }
    },
    methods: {
        onSearch({searchQuery, result}) {
            this.searchQuery = searchQuery
            this.result = result
            this.isShowResult = true
        },
        popstate(e) {
            if (!location.hash || location.hash === '#/') {
                this.isShowResult = false
            }
            if (location.hash === '#/result' && !this.isShowResult) {
                this.onSearch(e.state)
            }
        }
    },
    mounted() {
        if (location.hash !== '#/') {
            history.replaceState(null, '', '#/')
        }
        window.addEventListener('popstate', this.popstate)
    },
    beforeDestroy() {
        window.removeEventListener('popstate', this.popstate)
    }
}
</script>

<style>
    #app {
        padding: .2rem;
    }
</style>
